<template>
  <div class="auction-detail">
    <h1>拍卖详情</h1>
    <p>商品名称: {{ auction.goods_name }}</p>
    <p>起拍价: {{ auction.starting_price }}</p>
    <p>竞拍时间: {{ auction.time_long }} 分钟</p>
    <label for="bid">出价:</label>
    <input v-model="bidAmount" type="number" id="bid" />
    <button @click="placeBid">提交出价</button>
  </div>
</template>

<script>
export default {
  name: "AuctionDetail",
  data() {
    return {
      auction: {},
      bidAmount: 0,
    };
  },
  async created() {
    const auctionId = this.$route.params.id;
    try {
      const response = await fetch(`http://localhost:5000/auction/${auctionId}`);
      this.auction = await response.json();
    } catch (error) {
      console.error("获取拍卖详情失败:", error);
    }
  },
  methods: {
    async placeBid() {
      const auctionId = this.$route.params.id;
      try {
        const response = await fetch("http://localhost:5000/bid", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            auction_id: auctionId,
            buyer_id: 1, // 替换为实际的用户ID
            bid_amount: this.bidAmount,
          }),
        });

        const result = await response.json();
        if (response.ok) {
          alert("出价成功！");
        } else {
          alert(result.error || "出价失败");
        }
      } catch (error) {
        console.error("出价失败:", error);
      }
    },
  },
};
</script>

<style scoped>
.auction-detail {
  padding: 20px;
}
</style>
