<template>
  <div class="goods-list">
    <h1>商品列表</h1>
    <ul>
      <li v-for="item in goods" :key="item.id">
        <h2>{{ item.name }}</h2>
        <p>价格: {{ item.price }}</p>
        <button @click="viewAuction(item.id)">查看拍卖</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "GoodsList",
  data() {
    return {
      goods: [],
    };
  },
  async created() {
    try {
      const response = await fetch("http://localhost:5000/goods");
      const result = await response.json();
      this.goods = result;
    } catch (error) {
      console.error("获取商品列表失败:", error);
    }
  },
  methods: {
    viewAuction(goodsId) {
      this.$router.push(`/auction-detail/${goodsId}`);
    },
  },
};
</script>

<style scoped>
.goods-list {
  padding: 20px;
}
</style>
